                                      const allHide = document.querySelectorAll('.hide')
                                      if(allHide.length > 0){
                                        for (let i = 0; i < allHide.length; i++) {
                                          const element = allHide[i];
                                          if(element.classList.contains('hide')){
                                            element.classList.remove('hide');
                                          }
                                        }
                                      }else{
                                        setScoreState(scoreState + 1)
                                      }
