                                      MySwal.fire({
                                        position: 'top-end',
                                        timer: 2000,
                                        icon: 'info',
                                        titleText: "ไม่ถูกนะลองดูใหม่",
                                        toast: true,
                                        timerProgressBar: true
                                      })
                                      el.currentTarget.classList.add('hide')
