                                    const info = el.target.getAttribute('data-info');
                                    const result = questionData[gameState].answer.find(el => el[0] === info)
                                    setgameState(gameState + 1);
                                    if(result[1]){
                                      setScoreState(scoreState + 1)
                                    }
